$frontMainColor: #AF011C;
$frontMainBackColor: #fff8f8;
$frontSubColor:#e96464;
$mainFontColor: #373737;

body {
    margin: 0;
    font-family: 'M PLUS Rounded 1c';

    header {
        background-color: #fff;
        height: 100px;
        width: 100%;
        position: fixed;
        top: 0;
        z-index: 99;
        transition: transform 0.5s 0.2s;

        &.hidden {
            transform: translateY(-100px);
        }

        .header_upper {
            height: 50px;
            display: flex;
            box-sizing: border-box;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            padding: 4px;

            .header_upper_left {

                img {
                    &.logo_mark {
                        width: 40px;
                        vertical-align: bottom;
                    }
    
                    &.logo_type {
                        width: 160px;
                        vertical-align: initial;
                    }
                }
                   
            }

            .header_upper_right {
                display: flex;
                padding-top: 8.5px;

                a {
                    text-align: center;
                    text-align: -webkit-center;
                    text-decoration: none;
                    padding-right: 5px;
                    color: $mainFontColor;

                    img {
                        width: 20px;
                        display: block;
                    }                    
     
                    span {
                        font-size: 11.5px;
                        line-height: 1.4;
                    }
                }
            }
        }

        .header_downner {
            height: 50px;
            padding: 5px 11px;
            align-items: center;

            form {
                display: inline-block;
                width: 80%;

                input[type=text] {
                    height: 35px;
                    vertical-align: -webkit-baseline-middle;
                    width: 80%;
                }
    
                button {
                    background: #FFF;
                    border: none;
                    vertical-align: -webkit-baseline-middle;
    
                    img {
                        width: 20px;
                    }
                }    
            }

            a {
                display: inline-block;
                vertical-align: -webkit-baseline-middle;
                font-size: 11.5px;
            }
        }
    }
    
    main {
        padding-top: 100px;

        section {
            margin-bottom: 60px;
        }

        .swiper {
            height: 40vw;
            max-height: 600px;
            min-height: 400px;
            
            .swiper-slide {
                display: flex;
                justify-content: center;

                img {
                    align-self: center;
                    max-height: 100%;
                    max-width: 100%;
                    width: auto;
                }    
            }
        }

    }
}

